const faqFlyout = document.querySelector(".faq__flyout");
const kingRex = document.querySelector("[data-rex-about]");
const skullsRight = document.querySelector("[data-skulls-right]");
const skullsLeft = document.querySelector("[data-skulls-left]");


const handleScrollY = (entry, threshold) => {
	const { top, height } = entry.getBoundingClientRect();
	const windowHeight = window.innerHeight;
	let scrollProgress;

	if (top < windowHeight && top + height > 0) {
			scrollProgress = ((windowHeight - top) / (height + windowHeight)) * threshold;
	} else if (top + height < 0) {
			scrollProgress = 0;
	} else {
			scrollProgress = threshold;
	}

	entry.style.transform = `translateY(-${scrollProgress}%)`;
	console.log(`translate: ${scrollProgress}`);
};

// Intersection observer for X axis
const observerY = new IntersectionObserver(
	(entries) => {
			entries.forEach((entry) => {
					if (entry.isIntersecting) {
					const { target } = entry;
            if (target === kingRex) {
							window.addEventListener("scroll", () => handleScrollY(kingRex, 30));
            } else if (target === skullsLeft || target === skullsRight) {
							window.addEventListener("scroll", () => handleScrollY(target, 75));
            }
					} else {
							window.removeEventListener("scroll", handleScrollY());
					}
			});
	},
	{ rootMargin: "100px", threshold: [0, 1] }
);



