// // Observe all relevant elements
observerY.observe(kingRex);
observerY.observe(skullsLeft);
observerY.observe(skullsRight);

// Handle X axis scroll
const handleScrollX = () => {
    const { top, height } = faqFlyout.getBoundingClientRect();
    const windowHeight = window.innerHeight;
    let scrollProgress;

    if (top < windowHeight && top + height > 0) {
        scrollProgress = ((windowHeight - top) / (height + windowHeight)) * 100;
    } else if (top + height < 0) {
        scrollProgress = 0;
    } else {
        scrollProgress = 100;
    }

    faqFlyout.style.transform = `translateX(${scrollProgress}%)`;
    console.log(`translate: ${scrollProgress}`);
};

// Intersection observer for X axis
const observerX = new IntersectionObserver(
    (entries) => {
        entries.forEach((entry) => {
            if (entry.isIntersecting) {
                window.addEventListener("scroll", handleScrollX);
            } else {
                window.removeEventListener("scroll", handleScrollX);
            }
        });
    },
    { rootMargin: "0px 0px 0% 0px" }
);

// Observe faqFlyout for X axis scroll
observerX.observe(faqFlyout);

