// Get the text field
const copyText = document.querySelector(".token__test").textContent
const copyButton = document.querySelector(".token__btn")
const copyTimer = document.querySelector(".copied__text-timer")

copyButton.addEventListener("click", () => {
	console.log(copyText)

	// Copy the text inside the text field
	navigator.clipboard.writeText(copyText)

	// Alert the copied text
	// alert(`Copied the text: ${copyText}`)
	document.querySelector(".copied__text-main").textContent =
		`Copied text: ${copyText}`

	// decreaseWidthOverTime(copyTimer, 4)
	// copyTextContainer.classList.add(".copied__text-timer")

	copyTextContainer.classList.add("copied__text--active")

	setTimeout(function () {
		copyTextContainer.classList.remove("copied__text--active")
	}, 2000) // 2000 milliseconds = 2 seconds
})

