// Ensure the script runs after the DOM has loaded
document.addEventListener('DOMContentLoaded', function() {

  // Get a reference to the button element using its class
  const startButton = document.querySelector("[data-continue]");
	document.querySelector(".loaded").classList.remove("loaded--inactive")

  // Add a click event listener to the button
  startButton.addEventListener("click", function(event) {
    // event.preventDefault(); // prevent any default action of the anchor tag

		document.querySelector(".loaded").classList.add("loaded--inactive")

    // Create an audio element
    const audio = new Audio("../assets/dino.mp3");

    // Enable looping for the audio, if desired
    audio.loop = true;

    // Play the audio file
    audio.play();
  });

});


